<template>
  <Header />
  <div class="flex justify-center">
    <div class="w-full main">
      <div class="mb-2">
        <div class="flex ">
          <input
            placeholder="Add new word here"
            class="px-4 py-2 mr-2 rounded-full shadow w-full border border-transparent focus:outline-none focus:ring-2 focus:ring-purple-800 focus:border-transparent "
            v-model="wordNew"
            @focus="clearFormMessages"
          />
          <button
            @click="add(wordNew)"
            class="px-4 py-2 flex items-center bg-purple-600 shadow hover:bg-purple-700 text-white rounded-full focus:outline-none focus:ring-2 focus:ring-purple-600 focus:ring-opacity-50 ..."
            :disabled="!wordNew"
          >
            <span
              v-if="loadingSearch"
              class="flex items-center justify-center w-16"
              ><i class="fas fa-spinner animate-spin"></i
            ></span>
            <span v-else class="flex items-center justify-center w-16">
              <i class="fas fa-plus mr-2"></i> Add</span
            >
          </button>
        </div>
        <!-- <small class="text-white ml-4" v-if="formError">{{ formError }}</small>
        <small class="text-white ml-4" v-if="formSuccess">{{
          formSuccess
        }}</small> -->
      </div>
      <div class="rounded-xl w-full table shadow-sm border bg-white">
        <div
          v-if="myCollections.length === 0"
          class="text-center py-4 text-gray-400 font-bold"
        >
          No data
        </div>

        <table class="table-auto text-gray-700 w-full " v-else>
          <thead>
            <tr>
              <th
                class="border-b-2 border-r-2 px-4 py-1 rounded text-left w-full"
              >
                Word
              </th>
              <th class="border-b-2 px-4 py-1 rounded text-left ">
                <button
                  class=" text-red-500 whitespace-nowrap text-xs"
                  @click="removeAll"
                >
                  Delete all
                </button>
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(myCollection, index) in myCollections" :key="index">
              <td class="border-t-2 border-r-2 px-4 w-full">
                {{ myCollection }}
              </td>
              <td class="border-t-2 px-4 text-center">
                <button class="text-red-500" @click="remove(myCollection)">
                  <i class="fas fa-trash-alt"></i>
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "./Header";
export default {
  components: {
    Header,
  },
  data() {
    return {
      wordNew: "",
      myCollections: [],
      formError: null,
      formSuccess: null,
      loadingSearch: false,
    };
  },
  methods: {
    async searchWord(word) {
      const response = await fetch(
        `https://wordsapiv1.p.rapidapi.com/words/${word}/`,
        {
          method: "GET",
          headers: {
            "x-rapidapi-key":
              "fa03bc319cmsh84c9a1f6576ed63p105974jsnbd1e35e4ba8b",
            "x-rapidapi-host": "wordsapiv1.p.rapidapi.com",
          },
        }
      );
      const wordFound = await response.json();

      return wordFound;
    },
    async add(word) {
      this.loadingSearch = true;
      let wordSearched;
      wordSearched = await this.searchWord(word);

      const wordExisting = this.myCollections.find((value) => {
        return value.toLowerCase() == word.toLowerCase();
      });
      if (wordExisting) {
        this.$toast.error(`"${wordExisting}" already exists `, {
          position: "top-right",
        });
      } else {
        if (wordSearched.word) {
          try {
            const wordSearched = await this.searchWord(word);
            console.log(wordSearched);
          } catch (err) {
            console.log(err.message);
          }
          let myCollections = this.getMyCollections();
          if (myCollections === null) {
            myCollections = [word];
            localStorage.setItem(
              "myCollections",
              JSON.stringify(myCollections)
            );
          } else {
            myCollections = JSON.parse(myCollections);
            myCollections.push(word);
            localStorage.setItem(
              "myCollections",
              JSON.stringify(myCollections)
            );
          }
          this.$toast.success(`"${word}" successfully added 😃`, {
            position: "top-right",
          });
          this.wordNew = "";
          this.loadMyCollections();
        } else {
          this.$toast.error(`"${word}" not found 🙁`, {
            position: "top-right",
          });
        }
      }
      this.loadingSearch = false;
    },
    remove(wordIndex) {
      const index = this.myCollections.findIndex((index) => {
        return index == wordIndex;
      });
      this.myCollections.splice(index, 1);
      localStorage.setItem("myCollections", JSON.stringify(this.myCollections));
    },
    removeAll() {
      this.myCollections = [];
      localStorage.setItem("myCollections", JSON.stringify(this.myCollections));
    },
    getMyCollections() {
      return localStorage.getItem("myCollections");
    },
    loadMyCollections() {
      this.myCollections = JSON.parse(this.getMyCollections()).reverse() || [];
    },
    clearFormMessages() {
      this.formError = null;
      this.formSuccess = null;
    },
  },
  created() {
    this.loadMyCollections();
  },
};
</script>

<style>
.main {
  max-width: 600px;
}
</style>
